import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

// query background image
const data = useStaticQuery(graphql`
query {
    lgInterview: file(relativePath: { eq: "home/reviews/interview.png" }) {
        childImageSharp {
            fluid(quality: 90, maxWidth: 1400) {
                ...GatsbyImageSharpFluid
            }
        }
    },
    lgReview: file(relativePath: { eq: "home/reviews/review.png" }) {
        childImageSharp {
            fluid(quality: 90, maxWidth: 1400) {
                ...GatsbyImageSharpFluid
            }
        }
    }
}
`)

const BackgroundReview = () => {
    
    const sources = [
        // {
        //     ...data.mob.childImageSharp.fluid,
        //     media: `(max-width: 600px)`
        // },
        // {
        //     ...data.sm.childImageSharp.fluid,
        //     media: `(max-width: 850px)`
        // },
        // {
        //     ...data.med.childImageSharp.fluid,
        //     media: `(max-width: 1250px)`
        // },
        data.lgReview.childImageSharp.fluid,
    ]

    const wrapperStyle = {
        height: "100%",
    }

    return <Img fluid={sources} style={wrapperStyle}/>
}

const BackgroundInterview = () => {
    
    const sources = [
        // {
        //     ...data.mob.childImageSharp.fluid,
        //     media: `(max-width: 600px)`
        // },
        // {
        //     ...data.sm.childImageSharp.fluid,
        //     media: `(max-width: 850px)`
        // },
        // {
        //     ...data.med.childImageSharp.fluid,
        //     media: `(max-width: 1250px)`
        // },
        data.lgInterview.childImageSharp.fluid,
    ]

    const wrapperStyle = {
        height: "100%",
    }

    return <Img fluid={sources} style={wrapperStyle}/>
}

export default {
    BackgroundReview,
    BackgroundInterview
}
